.github-button {
    background-color: #4f527e;
    color: white; 
    width: 100px; 
    height: 50px;
    cursor: pointer;
    font-size: 25px;
    margin-top: 5px;
    place-items: center;
    margin-bottom: 5px;

}

.github-button:hover {
    background-color: #5E6194;
    transition-duration: 0.3s;
}

